const resize = (() => {
    /* Custom set rate element */
    function setRateElement(objects, x, y) {
        var w = x || 5;
        var h = y || 3;
        objects.height(objects.width() * h / w);
    }
    setRateElement($('.jsSetRate-1-1'), 1, 1);
    setRateElement($('.jsSetRate-8-5'), 8, 5);
    setRateElement($('.jsSetRate-9-10'), 9, 10);
    setRateElement($('.jsSetRate-55-21'), 55, 21);
    setRateElement($('.jsSetRate-5-3'));

    /* Browser resize */
    let timer;
    $(window).resize(function () {
        clearTimeout(timer);
        timer = setTimeout(function () {
            setRateElement($('.jsSetRate-1-1'), 1, 1);
            setRateElement($('.jsSetRate-8-5'), 8, 5);
            setRateElement($('.jsSetRate-9-10'), 9, 10);
            setRateElement($('.jsSetRate-55-21'), 55, 21);
            setRateElement($('.jsSetRate-5-3'));
        }, 300);
    });
})();

export default resize;


