"use strict";
import resize from './resize.js';

// $('.ui.dropdown').dropdown();

/* Menu outer */
$('.jsHamburger').click(function () {
    if (!$(this).hasClass('is-active')) {
        $(this).addClass('is-active');
        $('.jsHeaderOuter').addClass('overlay');
        $('.overlay').height($('.banner').height());
        $('.jsHeaderOuterMenu').transition('scale');
    } else {
        $(this).removeClass('is-active');
        $('.jsHeaderOuter').removeClass('overlay');
        $('.jsHeaderOuter').height(0);
        $('.jsHeaderOuterMenu').transition('scale');
    }
});


/* Browser resize */
let timer;
$(window).resize(() => {
    clearTimeout(timer);
    timer = setTimeout(() => {
        $('.overlay').height($('.banner').height());
    }, 300);
});

/* Owlcarousel2 slide - night page */
$(".nightOwlCarousel").owlCarousel({
    center: true,
    loop: true,
    autoplay: true,
    margin: 30,
    responsive: {
        0: {
            items: 1
        },
        575: {
            items: 2
        }
    },
    nav: true,
    navText: ['<i class="fa fa-angle-right" aria-hidden="true"></i>', '<i class="fa fa-angle-left" aria-hidden="true"></i>']
});

/* Owlcarousel2 slide - hole page */
$(".holeOwlCarousel").owlCarousel({
    center: true,
    loop: true,
    // autoplay: true,
    margin: 10,
    responsive: {
        0: {
            items: 1
        },
        576: {
            items: 3
        },
        768: {
            items: 5
        },
        992: {
            items: 7
        }
    }
});

$('.holeBarItem').click(function () {
    $('.holeOwlCarousel').trigger('to.owl.carousel',$('.holeBarItem').index(this));
});


